@charset "utf-8";

:root{
    --slide_delay_time:0.8s;
    --slide_time:24s;
}

@-webkit-keyframes from_top {
    0% {
        -webkit-transform :translate3d(0px, -100px, 0px);
    }
    100% {
        -webkit-transform :translate3d(0px, 0px, 0px);
    }
}

@keyframes from_top{
    0% {
        -webkit-transform :translate3d(0px, -100px, 0px);
                transform :translate3d(0px, -100px, 0px);
    }
    100% {
        -webkit-transform :translate3d(0px, 0px, 0px);
                transform :translate3d(0px, 0px, 0px);
    }
}

.from_top {
    -webkit-animation-name:from_top;
            animation-name:from_top;
    -webkit-animation-duration:1s;
            animation-duration:1s;
    -webkit-animation-iteration-count:1;
            animation-iteration-count:1;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
}

@-webkit-keyframes from_bottom {
    0% {
        -webkit-transform :translate3d(0px, 100px, 0px);
        opacity: 0;
    }
    100% {
        -webkit-transform :translate3d(0px, 0px, 0px);
        opacity: 1;
    }
}

@keyframes from_bottom{
    0% {
        -webkit-transform :translate3d(0px, 20px, 0px);
                transform :translate3d(0px, 20px, 0px);
        opacity: 0;
        pointer-events: none;
        cursor: inherit;
    }
    99% {
        cursor: inherit;
        pointer-events: none;
    }
    100% {
        -webkit-transform :translate3d(0px, 0px, 0px);
                transform :translate3d(0px, 0px, 0px);
        opacity: 1;
        pointer-events: inherit;
    }
}

@-webkit-keyframes from_bottom_deep {
    0% {
        -webkit-transform :translate3d(0px, 120px, 0px);
        opacity: 0;
    }
    100% {
        -webkit-transform :translate3d(0px, 0px, 0px);
        opacity: 1;
    }
}

@keyframes from_bottom_deep{
    0% {
        -webkit-transform :translate3d(0px, 120px, 0px);
                transform :translate3d(0px, 120px, 0px);
        opacity: 0;
        pointer-events: none;
        cursor: inherit;
    }
    50% {
        -webkit-transform :translate3d(0px, 80px, 0px);
                transform :translate3d(0px, 80px, 0px);
        opacity: 0;
    }
    100% {
        -webkit-transform :translate3d(0px, 0px, 0px);
                transform :translate3d(0px, 0px, 0px);
        opacity: 1;
        pointer-events: inherit;
    }
}

.from_bottom {
    -webkit-animation-name:from_bottom;
            animation-name:from_bottom;
    -webkit-animation-duration:0.4s;
            animation-duration:0.4s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    -webkit-animation-iteration-count:1;
            animation-iteration-count:1;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
}

.from_bottom_instantry {
    -webkit-animation-name:from_bottom_deep;
